html, body {
  background-color: #f5f5f5;
  font-family: Roboto, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

li a {
  color: #0288d1;
  text-decoration: underline;
}

.mdl-card {
  overflow: visible;
}

.grecaptcha-logo {
  background-color: #fff;
}

.mdl-grid {
  max-width: 1024px;
  margin: auto;
}

.mdl-layout__header-row {
  padding: 0;
}

.quickstart-user-details-container, .user-details-container {
  margin-top: 20px;
  line-height: 25px;
}

#quickstart-sign-in-status, #sign-in-status, #quickstart-tenant-id {
  font-weight: bold;
}

pre {
  line-height: 18px;
  overflow-x: scroll;
}

code {
  white-space: pre-wrap;
  word-break: break-all;
}

h3 {
  color: #fff;
  background: url("firebase-logo.43532bbd.png") 0 0 / 40px no-repeat;
  padding-left: 50px;
}

.close-icon {
  cursor: pointer;
  float: right;
  width: 20px;
}

.gcip-heading {
  background: none;
