}

#verification-code-form {
  display: none;
}

#recaptcha-container {
  margin-top: 10px;
  margin-bottom: 20px;
}

#verify-code-button, #cancel-verify-code-button {
  margin-left: 20px;
}

#sign-out-button {
  display: none;
}

#sign-in-card {
  z-index: 2;
}

.recaptcha-container {
  transform-origin: 0 0;
  transform: scale(.9);
}

.mfa-info-list-item {
  width: 300px;
  margin-top: 0;
  margin-bottom: 0;
}

.mdl-selectfield {
  height: 27px;
  width: 200px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.mdl-selectfield__select {
  box-shadow: none;
  box-sizing: border-box;
  height: 26px;
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, .26) 50%), linear-gradient(135deg, rgba(0, 0, 0, .26) 50%, rgba(0, 0, 0, 0) 50%), linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
  background-position: calc(100% - 10px) calc(1em - 4px), calc(100% - 5px) calc(1em - 4px), 100% 0;
  background-repeat: no-repeat;
  background-size: 5px 5px, 5px 5px;
  background-attachment: scroll, scroll, scroll;
  background-origin: padding-box, padding-box, padding-box;
  background-clip: border-box, border-box, border-box;
  border: 0 solid rgba(0, 0, 0, .12);
  border-bottom-width: 1px;
  border-radius: 0;
  margin: 0;
  padding-top: 4px;
  padding-bottom: 4px;
  line-height: 18px;
  outline: none !important;
}

.mdl-selectfield__label {
  color: rgba(0, 0, 0, .26);
  pointer-events: none;
  text-align: left;
  white-space: nowrap;
  width: 100%;
  font-size: 16px;
  transition-duration: .2s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
  top: -23px;
  left: 0;
  overflow: hidden;
}

.mdl-selectfield__label.is-active {
  color: #ff9800;
  font-size: 12px;
  top: -40px;
}

.blinking {
  animation: blinkingText 1.2s infinite;
}

@keyframes blinkingText {
  0% {
    color: red;
  }

  49% {
    color: rgba(0, 0, 0, 0);
  }

  50% {
    color: rgba(0, 0, 0, 0);
  }

  99% {
    color: red;
  }

  100% {
    color: red;
  }
}

#quickstart-tenant-card {
  min-height: 500px;
}

#quickstart-tenant-modal-title {
  text-align: center;
  margin: 0;
  padding: 2px;
}

